import { Button, Card, Col, Image, Row, Typography } from "antd";
import Meta from "antd/lib/card/Meta";
import React, { useState } from "react";
import { useSelector } from "react-redux";
import { NavLink } from "react-router-dom";

const Favorite = () => {
  const { user } = useSelector((state) => state.auth);
  const [favoriteMovies, setFavoriteMovies] = useState(JSON.parse(localStorage.getItem(user.email)) || []);

  const handleClick = () => {
    localStorage.removeItem(user.email);
    setFavoriteMovies([]);
  };

  return (
    <div>
      {!favoriteMovies || favoriteMovies.length === 0 ? (
        <Typography.Title level={1} style={{ textAlign: "center" }}>
          No featured films
        </Typography.Title>
      ) : (
        <>
          <div style={{ textAlign: "end", marginBottom: "40px", marginRight: "40px" }}>
            <Button type="primary" danger shape="round" onClick={() => handleClick()}>
              Очистить
            </Button>
          </div>
          <Row gutter={[0, 30]}>
            {favoriteMovies.map((movie) => {
              return (
                <Col
                  key={movie.imdbID}
                  span={6}
                  style={{
                    display: "flex",
                    position: "relative",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                >
                  <NavLink to={`/movie/${movie.imdbID}`}>
                    <Card
                      hoverable
                      cover={
                        <Image
                          alt={movie.Title}
                          preview={false}
                          src={movie.Poster}
                          fallback="data:image/png;base64,"
                        />
                      }
                      style={{ position: "relative", width: "200px" }}
                    >
                      <Meta title={movie.Title} description={movie.Year} />
                    </Card>
                  </NavLink>
                </Col>
              );
            })}
          </Row>
        </>
      )}
    </div>
  );
};

export default Favorite;
