import { Image, Space, Spin, Typography } from "antd";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { HeartFilled, HeartOutlined } from "@ant-design/icons";
import { useParams } from "react-router";
import { useActions } from "../../hooks/useActions";
import style from "./MovieCard.module.css";

const { Title, Paragraph, Text } = Typography;

const MovieCard = () => {
  const getFavoriteMovies = () => JSON.parse(localStorage.getItem(user.email)) || [];
  window.scrollTo(0, 0);
  const { id } = useParams();
  const { movieData, isLoading, isError } = useSelector((state) => state.movieID);
  const { isAuth, user } = useSelector((state) => state.auth);
  const [isSelected, setIsSelected] = useState(getFavoriteMovies().some((fItemID) => fItemID.imdbID === id));
  const { getMovieIDData } = useActions();

  useEffect(() => {
    getMovieIDData(id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id]);

  const content = [
    "Released",
    "Country",
    "Director",
    "Type",
    "Genre",
    "Rated",
    "Runtime",
    "Metascore",
    "imdbRating",
    "Actors",
  ];

  if (isLoading) {
    return (
      <div className={style.wrap}>
        <Spin size="large" />
      </div>
    );
  }
  if (isError) {
    return (
      <div className={style.wrap}>
        <h1>{isError}</h1>;
      </div>
    );
  }

  const handleClick = () => {
    const curItems = getFavoriteMovies();
    localStorage.setItem(
      user.email,
      isSelected
        ? JSON.stringify(curItems.filter((movieID) => movieID.imdbID !== id))
        : JSON.stringify([...curItems, movieData])
    );
    setIsSelected((v) => !v);
  };

  return (
    <div className={style.wrap}>
      <Title level={2}>
        {movieData.Title}
        {isAuth &&
          (isSelected ? (
            <HeartFilled className={style.favorite} style={{ color: "red" }} onClick={handleClick} />
          ) : (
            <HeartOutlined className={style.favorite} onClick={handleClick} />
          ))}
      </Title>
      <Space size="large" style={{ alignItems: "center", margin: "20px 0" }}>
        <Image
          alt={movieData.Title}
          src={movieData.Poster}
          preview={false}
          style={{ width: "250px" }}
          fallback="data:image/png;base64,"
        />
        <div style={{ fontSize: "16px" }}>
          {content.map((item) => {
            return (
              <Paragraph key={item}>
                <Text strong>{item}: </Text>
                <Text>{movieData[item]}</Text>
              </Paragraph>
            );
          })}
        </div>
      </Space>
      <div className={style.description}>
        <Title level={4}>About "{movieData.Title}": </Title>
        <Text italic>{movieData.Plot}</Text>
      </div>
    </div>
  );
};

export default MovieCard;
